body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  overflow: hidden;
}

.map {
  width: 100vw;
  height: 100vh;
}
.location-marker-ice {
  transform: rotate(180deg);
  transform-origin: center;
}
.location-icon-ice {
  color: rgb(165, 242, 243);
  stroke: black;
  stroke-width: 5;
}
.location-icon-fire {
  font-size: 2rem;
  color: red;
}
.location-icon-volcano {
  font-size: 2rem;
  color: brown;
}
.location-icon-ice {
  font-size: 2rem;
}
.header {
  z-index: 20;
  padding: 10px;
  width: 100vw;
  background-color: firebrick;
  color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header h1 {
  font-size: 25px;
  padding: 0;
  margin: 0;
  text-align: center;
}

.loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}
.loader img {
  width: 400px;
}
.loader h1 {
  margin-top: -100px;
}

.location-info {
  position: absolute;
  top: 80px;
  right: 50px;
  width: 400px;
  min-height: 200px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  font-size: 18px;
  color: #fff;
}

.location-info ul {
  list-style-type: none;
  padding: 0;
}

.location-info li {
  padding: 5px 0;
}

.toggles {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 10px;
  width: 70px;
  position: absolute;
  top: 45px;
  height: 150px;
  background-color: firebrick;
  z-index: 5;
}

.toggles label {
  display: flex;
  flex-direction: row;
  align-items: center;
}

@media (max-width: 600px) {
  .toggles {
    width: 100%;
    height: 50px;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
  }
  .location-info {
    top: initial;
    bottom: 0;
    width: 90%;
    margin: auto;
    left: 0;
    right: 0;
    min-height: initial;
  }
}

@media (max-width: 510px) {
  .header h1 {
    font-size: 18px;
  }
}
